/* FLEX HELPERS */

.flex {
  display: flex;
}

.column {
  flex-direction: column;
}

.flex.space-between {
  justify-content: space-between;
}

.flex.justify-center {
  justify-content: center;
}

.flex.justify-end {
  justify-content: flex-end;
}

.flex.justify-start {
  justify-content: flex-start;
}

.flex.align-center {
  align-items: center;
}
.flex.align-start {
  align-items: flex-start;
}

.flex.flex-grow {
  flex: 1;
}

/* LIST HELPERS */

.clean-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

/* BUTTON HELPERS */

.el-close {
  position: absolute;
  z-index: 1;
  top: 8px;
  right: 8px;
  background-color: hsla(0, 0%, 100%, 0.24);
  border: none;
  &:hover {
    background-color: rgba(9, 30, 66, 0.08);
    border: none;
  }
  &:focus {
    background-color: rgba(9, 30, 66, 0.08);
    border: none;
  }
}

.btn {
  // margin-block-start: 10px;
  width: 100px;
  height: 35px;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
  line-height: 16px;
  outline: none;
  border: none;
  background-color: #d6d6d6;
  text-align: center;
}

.icon {
  width: 45px;
}

.back-icon {
  position: absolute;
  top: 100px;
  left: 90px;
}

